.footer-section {
  background-color: #36082c;
  background-image: linear-gradient(315deg, #31021c 0%, #270317 74%);
  position: relative;
  margin-top: 1rem;
}
.footer-cta {
  border-bottom: 1px solid #373636;
}

.footer-content {
  position: relative;
  z-index: 2;
  margin: auto;
}

.footer-logo {
  margin-bottom: 40px;
  text-align: left;
  color: white;
}
.footer-logo h3 {
  text-align: left;
  color: white;
}
.footer-logo img {
  max-width: 200px;
}
.footer-text p {
  margin-bottom: 14px;
  font-size: 14px;
  color: #7e7e7e;
  line-height: 28px;
}
.footer-social-icon span {
  color: #fff;
  display: block;
  font-size: 20px;
  font-weight: 700;
  font-family: "Poppins", sans-serif;
  margin-bottom: 20px;
}
.footer-social-icon a {
  color: #fff;
  margin-right: 15px;
}
.footer-social-icon a:hover {
  color: #f12222;
}
.footer-social-icon i {
  height: 40px;
  width: 40px;
  text-align: center;
  line-height: 38px;
  border-radius: 50%;
}

.footer-widget-heading h3 {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  margin-top: 35px;
  margin-bottom: 40px;
  position: relative;
}
.footer-widget-heading h3::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  height: 2px;
  width: 7rem;
  background: #a33807;
}
.footer-widget ul li {
  display: inline-block;
  width: 50%;
  margin-bottom: 12px;
}
.footer-widget ul li a:hover {
  color: #ff5e14;
}
.footer-widget ul li a {
  color: #878787;
  text-transform: capitalize;
  text-decoration: none;
}

.copyright-area {
  background: #1a1919;
  padding: 25px 0;
}
.copyright-text p {
  margin: 0;
  font-size: 14px;
  color: #878787;
}
.copyright-text p a {
  color: #923509;
}
.footer-menu li {
  display: inline-block;
  margin-left: 20px;
}
.footer-menu li:hover a {
  color: #ff5e14;
}
.footer-menu li a {
  font-size: 14px;
  color: #878787;
}
.copyright-text a {
  text-decoration: none;
  font-weight: 500;
}
.footer-widget-heading h3 {
  margin-top: 0;
}
.contacts {
  color: #878787;
}
