@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&family=Redressed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Redressed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@700&family=Open+Sans:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bangers&family=Jura:wght@600&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ad021f;
  background: linear-gradient(to right, #2e0a47, #c31432);
  /* background-image: linear-gradient(to right, #192496, #1e35a2, #2645ad, #2f55b8, #3a64c2,
   #4267c4, #4969c6, #4f6cc8, #5362c2, #5857bb, #5e4bb3, #633faa); */
  font-family: "Raleway", sans-serif;
  color: white;
}

.navbar .navbar-brand {
  font-size: 3rem;
  color: #ffffff;
  border-bottom: none;
  font-family: "Redressed", "Josefin Sans", "Open Sans";
}
.navbar .navbar-brand:hover {
  color: #dda2d5;
}
.navbar a {
  font-size: 1.3rem;
}
.navbar a:hover {
  color: #330b2e;
}
.active {
  border-bottom: 2px solid #e0b8db;
}

nav.navbar.navbar-expand-lg.navbar-light.bg-white {
  box-shadow: none;
}
a.navbar-brand.font-weight-bolder.active {
  font-size: 1.9rem;
  font-weight: 400;
  color: #ffffff;
  border-bottom: none;
}
.navbar-nav {
  margin-left: auto;
}

.nav-link,
.toggler {
  color: rgb(255, 224, 224);
}
.nav-item {
  margin: 0.3rem;
}
.navbar-toggler {
  background-color: transparent !important;
  padding-bottom: 8px !important;
  align-content: center;
  border: 2px solid #fac1c1 !important;
}
@media screen and (max-width: 450px) {
  .invisiblle {
    display: none !important;
  }
  .navbar .navbar-brand {
    font-size: 2.68rem;
  }
}

#header {
  /* width: 100%; */
  height: 75vh;
  margin-top: 1rem;
  color: white;
}
#header h1 {
  color: #ffffff;
  margin: 0 0 10px 0;
  font-size: 1.6rem;
  font-weight: 400;
  margin-left: 1rem;
}
#header h2 {
  color: #ffffff;
  font-size: 0.85rem;
  margin-left: 1rem;
}
#header .btn-started {
  margin-left: 1rem;
  align-items: center;
  font-weight: 500;
  font-size: 0.85rem;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.7s;
  color: #ffffff;
  border: 2px solid #ffffff;
  text-decoration: none;
}
#header .btn-started:hover {
  background-color: #d8b6bb;
  color: #1b1a1a;
}
#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
  height: 400px;
  width: 400px;
  margin-left: 6rem;
  margin-bottom: 4rem;
}
.textprimary {
  color: #e597ad;
  text-decoration: none;
  font-weight: 500;
}
.textprimary:hover {
  color: #740a50;
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
  text-decoration: none;
  font-weight: 500;
}
@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}
.col-lg-6.col-md-12.order-1.order-lg-2.align-items-center {
  align-self: center;
}
@media screen and (max-width: 990px) {
  .pixelScreen {
    margin-left: auto;
    margin-right: auto;
  }
}

.footer-section {
  background-color: #36082c;
  background-image: linear-gradient(315deg, #31021c 0%, #270317 74%);
  position: relative;
  margin-top: 1rem;
}
.footer-cta {
  border-bottom: 1px solid #373636;
}

.footer-content {
  position: relative;
  z-index: 2;
  margin: auto;
}

.footer-logo {
  margin-bottom: 40px;
  text-align: left;
  color: white;
}
.footer-logo h3 {
  text-align: left;
  color: white;
}
.footer-logo img {
  max-width: 200px;
}
.footer-text p {
  margin-bottom: 14px;
  font-size: 14px;
  color: #7e7e7e;
  line-height: 28px;
}
.footer-social-icon span {
  color: #fff;
  display: block;
  font-size: 20px;
  font-weight: 700;
  font-family: "Poppins", sans-serif;
  margin-bottom: 20px;
}
.footer-social-icon a {
  color: #fff;
  margin-right: 15px;
}
.footer-social-icon a:hover {
  color: #f12222;
}
.footer-social-icon i {
  height: 40px;
  width: 40px;
  text-align: center;
  line-height: 38px;
  border-radius: 50%;
}

.footer-widget-heading h3 {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  margin-top: 35px;
  margin-bottom: 40px;
  position: relative;
}
.footer-widget-heading h3::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  height: 2px;
  width: 7rem;
  background: #a33807;
}
.footer-widget ul li {
  display: inline-block;
  width: 50%;
  margin-bottom: 12px;
}
.footer-widget ul li a:hover {
  color: #ff5e14;
}
.footer-widget ul li a {
  color: #878787;
  text-transform: capitalize;
  text-decoration: none;
}

.copyright-area {
  background: #1a1919;
  padding: 25px 0;
}
.copyright-text p {
  margin: 0;
  font-size: 14px;
  color: #878787;
}
.copyright-text p a {
  color: #923509;
}
.footer-menu li {
  display: inline-block;
  margin-left: 20px;
}
.footer-menu li:hover a {
  color: #ff5e14;
}
.footer-menu li a {
  font-size: 14px;
  color: #878787;
}
.copyright-text a {
  text-decoration: none;
  font-weight: 500;
}
.footer-widget-heading h3 {
  margin-top: 0;
}
.contacts {
  color: #878787;
}

